*{
  margin: 10px;
}

body {
  margin: 0;
  padding: 0;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(27, 27, 27);

}

.app{
  width: 500px;
  background-color: #1A1B21;
  color: white;
  border-radius: 10px;
}

.self{
  margin: 0;
  width: 100%;
  border-radius: 10px 10px 0 0;
}

.main{
  padding: 15px;
}

.dev{
  color: orange;
}

.buttons{
  display: flex;
}

.in-btn{
  background-color: #5093E2;
  color: white;
}
.email-btn{
  background-color: rgb(238, 238, 238);
}

.in-btn, .email-btn{
  display: flex;
  align-items: center;
  height: 30px;
}

.info-1{
  display: flex;
  align-items: center;
  flex-direction: column;
}

footer{
  display: flex;
  justify-content: center;
  gap: 15px;
  margin: 0;
  border-radius: 0 0 10px 10px;
  height: 50px;
  background-color: #161619;
  width: 100%;
}